.navbar{
    /* border: 2px solid black; */
    width: 100vw;
    height: 50px;
    background-color: #FFE53B;
    background-image: linear-gradient(147deg, #FFE53B 0%, #ff7c25 74%);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar > h1{
    color: white;
    font-size: 1.5rem;
    margin-left: 15px;
    font-family: 'Comfortaa', cursive;
}

.navbar__item{
    margin-right: 40px !important;    
}

.navbar__link{    
   /* border: 2px solid black; */
   padding: 10px 10px;
   margin: 10px 10px;
   display: flex !important;
   justify-content: space-between !important;   
}

.navbar__link-active{
    text-decoration: none;
    color: white;
    font-family: 'Comfortaa', cursive !important;
}

.menu_active{
    font-weight: bold;
    border-bottom: 1px solid;
    font-family: 'Comfortaa', cursive !important;
}
   
.title{
    /* display: flex; */
    align-items: center;  
    justify-content: center;  
    /* border: 2px solid purple; */
}

.title > img{
    height: 500px;
    width: 100vw;
    margin-bottom: -4px;    
}

.title > h2{
    color: white;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: 'Comfortaa', cursive;
    font-size: xx-large;
    letter-spacing: 2px;
}
.menu{    
    /* border: 2px solid greenyellow; */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 190px;
    width: 100vw;
    background-color: #FFE53B;
    background-image: linear-gradient(147deg, #FFE53B 0%, #ff7c25 74%);
    /* background-color: #FFDEE9;
    background-image: linear-gradient(0deg, #FFDEE9 0%, #B5FFFC 100%);   */
}

.menu > h2{
    color: white;
    font-family: 'Dancing Script', cursive;
    font-size: xx-large;    
}
.footer{
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 45px;
    /* background-color: #FFE53B;
    background-image: linear-gradient(147deg, #FFE53B 0%, #ff7c25 74%); */
    background-color: #85FFBD;
    background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%);
}

.footer >p {
    font-family: 'Rubik', sans-serif; 
}
.home__chapter{
    width: 100vw;
    display: flex;  
    flex-wrap: wrap;
    justify-content: space-evenly;  
    /* background-color: #85FFBD;
    background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%); */
    background-color: #FEE140;
    background-image: linear-gradient(90deg, #FEE140 0%, #FA709A 100%);
}

/* .home__chapter{
  display: grid;
  grid: auto / auto auto auto;
  align-items: center;
  justify-content:space-evenly;
} */
.chapter__card{
    flex: 1 1;
    
    margin: 20px;
    max-width: 350px;
    max-height: 900px;
    min-width: 450px;   
    min-height: 980px;
}

.chapter__cardheading{
    font-family: 'Raleway', sans-serif !important;
}

.chapter__cardsummary{
    font-family: 'Raleway', sans-serif !important;     
}

.chapter__cardmedia{    
    height: 300px;
}

.MuiDivider-root{
    margin-bottom: 20px !important;
    margin-top: 20px !important;
    cursor: default !important;
}

.chapter__cardcontent{
    cursor: default !important;
}


.home__verse{
    width: 100vw;
    display: flex;  
    flex-wrap: wrap;
    justify-content: space-evenly; 
    background-color: #FEE140;
    background-image: linear-gradient(90deg, #FEE140 0%, #FA709A 100%); 
}
.verse__card{
    flex: 1 1;
    cursor: pointer;
    margin: 20px;
    max-width: 500px;
    max-height: 750px;
    min-width: 400px;
    min-height: 650px;
}
.verse__cardheading{
    font-family: 'Raleway', sans-serif !important;
}

.verse__cardsummary{
    font-family: 'Raleway', sans-serif !important;     
}
.verse__cardmedia{    
    height: 350px;
}

.MuiDivider-root{
    margin-bottom: 20px !important;
    margin-top: 20px !important;
}

.about{
    height: 340px;
    width: 100vw;
    /* border: 2px solid grey; */     
    background-color: #FFE53B;
    background-image: linear-gradient(147deg, #FFE53B 0%, #ff7c25 74%);
    color: #fff;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.about__content > h2{
    margin: 40px 100px;
    margin-bottom: 20px;
    font-family: 'Comfortaa', cursive;
}
.about__content > p{
    margin-left: 100px ;
    max-width: 300px;   
    font-family: 'Raleway', sans-serif;
    letter-spacing: 1px;
}

.about__img > img{
    object-fit: contain;
    height: 340px;
    mix-blend-mode: color-burn;    
}
